.club {
  background: var(--light-grey);
  padding: 50px 0 65px;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--white);
    padding: 75px 0 55px;
  }

  &__title {
    margin-bottom: 16px;
  }

  &__text {
    max-width: 470px;
    margin-bottom: 70px;
    text-align: center;
  }
}

@media (max-width: 1000px) {
  .club {
    background: none;
    padding: 0;

    &__container {
      padding: 40px 0 55px;
    }

    &__title {
      font-size: 20px;
    }

    &__text {
      font-size: 14px;
      text-align: start;
      margin-bottom: 62px;
    }
  }
}

@media (max-width: 500px) {
  .club {
    &__container {
      align-items: flex-start;
    }
  }
}
