.form {
  margin: 0 auto;
  display: flex;
  flex-wrap: nowrap;
}

.form--widht {
  margin: 0 auto;
}

@media (max-width: 1000px) {
  .form {
    &__btn {
      height: 56px;
      padding: 10px 30px;
    }
  }
}

@media (max-width: 500px) {
  .form {
    &__btn {
      padding: 3px 18px;
    }
  }
}
